import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {debounceTime, distinctUntilChanged, map, switchMap} from 'rxjs/operators';
import {Observable} from 'rxjs';


@Injectable()
export class VendorService {
  constructor(private http: HttpClient) {
  }

  search(terms: Observable<string>) {
    return terms.pipe(debounceTime(400),
      distinctUntilChanged(),
      switchMap(term => this.vendorList(term)));
  }

  vendorList(searchText?: string) {
    let url = '/api/admin_vendor_list/';
    if (searchText) {
      url += `?q=${searchText}`;
    }
    return this.http.get<any>(url).pipe(map(result => result['result']));
  }

  newVendor(formData) {
    return this.http.post<any>('/api/admin_vendor_list/', formData);
  }

  editVendor(formData) {
    return this.http.put<any>(`/api/admin_vendor_detail/${formData.id}/`, formData);
  }

  deleteVendor(id: number) {
    return this.http.delete<any>(`/api/admin_vendor_detail/${id}/`);
  }

  countries() {
    return this.http.get('/api/countries/');
  }
}
