import {Component, EventEmitter, OnDestroy, OnInit, Output} from '@angular/core';
import {BsModalRef} from 'ngx-bootstrap';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {VendorService} from '../services/vendor.service';
import {takeWhile} from 'rxjs/operators';
import {AlertService} from '../../shared/components/alert/alert.service';

@Component({
  selector: 'app-new-vendor',
  templateUrl: './new-vendor.component.html',
  styleUrls: ['./new-vendor.component.css'],
  providers: []
})
export class NewVendorComponent implements OnInit, OnDestroy {
  @Output() submitEvent: EventEmitter<any> = new EventEmitter<any>();
  form: FormGroup;
  alive = true;
  countries: Array<any> = [];
  _states: Array<any> = [];
  _cities: Array<any> = [];
  states: Array<any> = [];
  cities: Array<any> = [];
  start_time = '';
  end_time = '';

  constructor(
    public bsModalRef: BsModalRef,
    private fb: FormBuilder,
    private vendorService: VendorService,
    private alertService: AlertService
  ) {
    this.form = this.fb.group({
      id: null,
      name: ['', [Validators.required, Validators.pattern(/^[a-zA-Z-,]+(\s{0,1}[a-zA-Z-, ])*$/)]],
      email: ['', [Validators.required, Validators.email]],
      contact_name: [''],
      contact_title: [''],
      mobile_no: ['',
        [
          Validators.required,
          Validators.minLength(10),
          Validators.maxLength(10),
          Validators.pattern(/^-?(0|[1-9]\d*)?$/)
        ]
      ],
      address: ['', Validators.required],
      city: [null, Validators.required],
      state: [null, Validators.required],
      country: [null, Validators.required],
      postal_code: [''],
      notify_of_our_project: [''],
      return_consent_receive: [''],
      notes: '',
      start_time: [''],
      end_time: [''],
    });
  }

  ngOnInit() {
    this.getCountries();
  }

  ngOnDestroy() {
    this.alive = false;
  }

  getCountries(callback?) {
    this.vendorService.countries().pipe(takeWhile(() => this.alive)).subscribe(data => {
      this.countries = data['result']['countries'];
      this._states = data['result']['states'];
      this._cities = data['result']['cities'];
      this.form.patchValue({
        country: 101
      });
      this.onChangeCountry(101);
      if (this.form.get('state').value) {
        this.onStateChange(this.form.get('state').value);
      }
      if (callback) {
        callback();
      }
    });
  }

  editVendor(vendor: any) {
    this.form.patchValue({
      id: vendor.id,
      name: vendor.name,
      email: vendor.user.email,
      contact_name: vendor.contact_name,
      contact_title: vendor.contact_title,
      mobile_no: vendor.mobile_no,
      address: vendor.address,
      state: vendor.state,
      city: vendor.city,
      country: vendor.country,
      postal_code: vendor.postal_code,
      notify_of_our_project: vendor.notify_of_our_project,
      return_consent_receive: vendor.return_consent_receive,
      notes: vendor.notes,
      start_time: vendor.start_time,
      end_time: vendor.end_time,
    });
    this.start_time = this.form.get('start_time').value;
    this.end_time = this.form.get('end_time').value;
  }

  errorMessage(control: string, errorName: string) {
    return this.form.get(control).hasError(errorName) && (this.form.get(control).dirty || this.form.get(control).touched);
  }

  onChangeCountry(id) {
    this.states = [];
    if (id) {
      this.states = this._states.filter(value => value.country_id === +id);
    }
  }

  onStateChange(id) {
    this.cities = [];
    if (id) {
      this.cities = this._cities.filter(value => value.state_id === +id);
    }
  }

  setStartDate(event) {
    if (event) {
      this.form.patchValue({start_time: event});
    }
  }

  setEndDate(event) {
    this.form.patchValue({end_time: event});
  }

  onSubmit() {
    const formData = this.form.getRawValue();
    formData['start_time'] = this.convert12To24Hours(formData['start_time']);
    formData['end_time'] = this.convert12To24Hours(formData['end_time']);
    if (formData.id) {
      this.vendorService.editVendor(formData).pipe(takeWhile(() => this.alive)).subscribe(response => {
        if (response.error) {
          this.alertService.error(response.message);
        } else {
          this.bsModalRef.hide();
          this.alertService.success(response.message);
          this.submitEvent.emit(true);
        }
      });
    } else {
      this.vendorService.newVendor(formData).pipe(takeWhile(() => this.alive)).subscribe(response => {
        if (response.error) {
          this.alertService.error(response.message);
        } else {
          this.alertService.success(response.message);
          this.bsModalRef.hide();
          this.submitEvent.emit(true);
        }
      });
    }
  }

  convert12To24Hours(value) {
    const a = value.split(':');
    let hours = +a[0];
    const meridian = a[1].split(' ')[1];
    const minutes = +a[1].split(' ')[0];
    if (meridian === 'PM' && hours < 12) {
      hours = hours + 12;
    }
    if (meridian === 'AM' && hours === 12) {
      hours = hours - 12;
    }
    let sHours = hours.toString();
    let sMinutes = minutes.toString();
    if (hours < 10) {
      sHours = '0' + sHours;
    }
    if (minutes < 10) {
      sMinutes = '0' + sMinutes;
    }
    return `${sHours}:${sMinutes}`;
  }

}
