import {Component, OnDestroy, OnInit} from '@angular/core';
import {VendorService} from '../services/vendor.service';
import {BsModalRef, BsModalService} from 'ngx-bootstrap';
import {NewVendorComponent} from '../new-vendor/new-vendor.component';
import {debounceTime, distinctUntilChanged, map, switchMap, takeWhile} from 'rxjs/operators';
import {ConfirmAlertBoxService} from '../../shared/components/confirm-alert-box/confirm-alert-box.service';
import {AlertService} from '../../shared/components/alert/alert.service';
import {Observable, Subject} from 'rxjs';

@Component({
  selector: 'app-vendor-list',
  templateUrl: './vendor-list.component.html',
  styleUrls: ['./vendor-list.component.css']
})
export class VendorListComponent implements OnInit, OnDestroy {
  bsModalRef: BsModalRef;
  isAlive = true;
  vendors: Array<any> = [];
  private searchText$ = new Subject<string>();
  constructor(
    private vendorService: VendorService,
    private modalService: BsModalService,
    private confirmBoxService: ConfirmAlertBoxService,
    private alertService: AlertService,
  ) { }

  ngOnInit() {
    this.vendorService.search(this.searchText$).pipe(takeWhile(() => this.isAlive)).subscribe(response => {
      this.vendors = response;
    });
    this.getVendorList();
  }

  getVendorList() {
    this.vendorService.vendorList().pipe(takeWhile(() => this.isAlive)).subscribe(data => {
      this.vendors = data;
    });
  }

  newVendor(vendor?) {
    this.bsModalRef = this.modalService.show(NewVendorComponent, {
      backdrop: true,
      ignoreBackdropClick: true
    });
    if (vendor) {
      this.bsModalRef.content.editVendor(vendor);
    }
    this.bsModalRef.content.submitEvent.pipe(takeWhile(() => this.bsModalRef.content.alive)).subscribe(response => {
      this.getVendorList();
    });
  }

  deleteVendor(obj) {
    this.confirmBoxService.confirmBox({
      body: `Are you sure you wish to remove ${obj.name}?`,
      callback: () => {
        this.vendorService.deleteVendor(obj.id).pipe(takeWhile(() => this.isAlive)).subscribe(response => {
          if (response.error) {
            this.alertService.error(response.message);
          } else {
            this.alertService.success(response.message);
            this.getVendorList();
          }
        });
      }
    });
  }

  searchVendor(searchText) {
    this.searchText$.next(searchText);
  }

  ngOnDestroy() {
    this.isAlive = false;
  }

}
